header {
  width: 75%;
  min-height: 7em;
  display: flex;
  justify-content: space-between;
  margin: auto;
  border-bottom: 1px solid hsla(219, 35%, 92%, 1);
  align-items: center;
}

.logo-hamburger {
  display: flex;
  align-items: center;
  gap: 32px;
}

.hamburger {
  display: none;
  background: transparent;
  border: none;
  cursor: pointer;
}

.menu__list {
  display: flex;
  list-style: none;
  gap: 32px;
}

.menu__close {
  display: none;
  background: transparent;
  border: none;
}

.menu__list__items {
  position: relative;
  cursor: pointer;
}

.menu__list__items::after {
  position: absolute;
  content: '';
  width: 0;
  height: 4px;
  bottom: -2.875em;
  left: 50%;
  transform: translateX(-50%);
  transition: 0.4s ease;
  background-color: hsla(26, 100%, 55%, 1);
}

.menu__list__items:hover::after {
  width: 100%;
}

.user {
  display: flex;
  align-items: center;
  gap: 45px;
}

.user__cart {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.user__avatar {
  height: 50px;
  width: 50px;
}

.user__avatar:hover {
  border: 2px solid hsla(26, 100%, 55%, 1);
  border-radius: 50%;
  cursor: pointer;
}

@media (max-width: 64em) {
  header {
    width: 85%;
  }

  .menu {
    gap: 30px;
  }

  .menu__list {
    gap: 20px;
  }

  .user {
    gap: 20px;
  }
}

@media (max-width: 48em) {
  .hamburger {
    display: flex;
  }

  .menu {
    position: absolute;
    left: 0;
    right: 0;
    transform: translateY(-150%);
    background-color: hsla(0, 0%, 0%, 0.75);
    transition: 0.5s ease;
  }

  .menu.active {
    transform: translateY(0%);
  }

  .menu__list {
    width: 70%;
    flex-direction: column;
    align-items: center;
    background-color: hsla(0, 0%, 100%, 1);
  }

  .menu__list__items::after {
    display: none;
  }
}

@media (max-width: 25rem) {
  .logo-hamburger {
    gap: 16px;
  }

  .user {
    gap: 15px;
  }

  .user__avatar {
    width: 26px;
    height: 26px;
  }
}
